import { attr, Model } from 'redux-orm';

import {
  FETCH_UPDATE_INVITATION,
  FETCH_DELETE_INVITATION,
  FETCH_GET_ALL_INVITATIONS_SENT,
} from '../../constants/api/invitations';

class InvitationSent extends Model {
  static create(props) {
    if (props.hasOwnProperty(this.idAttribute) && this.idExists(props[this.idAttribute])) {
      const model = this.withId(props.id);
      return model.update(props);
    }
    return super.create(props);
  }

  static reducer(action, modelClass) {
    const { type, payload } = action;
    switch (type) {
      case `${FETCH_UPDATE_INVITATION}_SUCCESS`:
        modelClass.withId(payload.body.id).update(payload.body);
        break;
      case `${FETCH_DELETE_INVITATION}_SUCCESS`:
        modelClass.withId(payload.body.id).delete();
        break;
      case `${FETCH_GET_ALL_INVITATIONS_SENT}_SUCCESS`: {
        const array = payload.body.results || payload.body;
        for (let i = 0; i < array.length; i += 1) {
          const obj = array[i];
          modelClass.create(obj);
        }
        break;
      }
      default:
        break;
    }
    return null;
  }
}

InvitationSent.modelName = 'InvitationSent';

InvitationSent.fields = {
  id: attr(),
  email: attr(),
  role: attr(),
};

export default InvitationSent;
