import { fk, attr, Model } from 'redux-orm';

import {
  FETCH_CREATE_COMMENT,
  FETCH_GET_ALL_COMMENTS,
  FETCH_UPDATE_COMMENT,
  FETCH_DELETE_COMMENT,
} from '../../constants/api/comments';

class Comment extends Model {
  static create(props) {
    if (props.hasOwnProperty(this.idAttribute) && this.idExists(props[this.idAttribute])) {
      const model = this.withId(props.id);
      return model.update(props);
    }
    return super.create(props);
  }

  static reducer(action, modelClass) {
    const { type, payload } = action;
    switch (type) {
      case 'PUSHER_CREATE_TASK_COMMENT': {
        modelClass.create({ ...payload });
        break;
      }
      case 'PUSHER_DELETE_TASK_COMMENT': {
        modelClass.withId(payload.id).delete();
        break;
      }
      case 'PUSHER_UPDATE_TASK_COMMENT': {
        modelClass.withId(payload.id).update({ ...payload });
        break;
      }
      case `${FETCH_CREATE_COMMENT}_SUCCESS`: {
        const body = { ...payload.body, id: payload.body.id };
        modelClass.create(body);
        break;
      }
      case `${FETCH_UPDATE_COMMENT}_SUCCESS`:
        modelClass.withId(payload.body.id).update(payload.body);
        break;
      case `${FETCH_DELETE_COMMENT}_SUCCESS`:
        modelClass.withId(payload.body.id).delete();
        break;
      case `${FETCH_GET_ALL_COMMENTS}_SUCCESS`: {
        const array = payload.body;
        for (let i = 0; i < array.length; i += 1) {
          const obj = array[i];
          modelClass.create(obj);
        }
        break;
      }
      default:
        break;
    }
    return null;
  }
}

Comment.modelName = 'Comment';

Comment.fields = {
  id: attr(),
  text: attr(),
  created_at: attr(),
  user_id: attr(),
  task_id: fk('Task', 'comments'),
};

export default Comment;
