import { fk, attr, Model } from 'redux-orm';

import {
  FETCH_GET_CHECKLIST,
  FETCH_CREATE_CHECKLIST,
  FETCH_TOGGLE_CHECKLIST,
  FETCH_UPDATE_CHECKLIST,
  FETCH_DELETE_CHECKLIST,
} from '../../constants/api/checklist';

class Checklist extends Model {
  static create(props) {
    if (props.hasOwnProperty(this.idAttribute) && this.idExists(props[this.idAttribute])) {
      const model = this.withId(props.id);
      return model.update(props);
    }
    return super.create(props);
  }

  static reducer(action, modelClass) {
    const { type, payload } = action;
    switch (type) {
      case `${FETCH_CREATE_CHECKLIST}_SUCCESS`: {
        const body = { ...payload.body, id: payload.body.id };
        modelClass.create(body);
        break;
      }
      case `${FETCH_TOGGLE_CHECKLIST}_SUCCESS`:
        modelClass.withId(payload.body.id).update(payload.body);
        break;
      case `${FETCH_UPDATE_CHECKLIST}_SUCCESS`:
        modelClass.withId(payload.body.id).update(payload.body);
        break;
      case `${FETCH_DELETE_CHECKLIST}_SUCCESS`:
        modelClass.withId(payload.body.id).delete();
        break;
      case `${FETCH_GET_CHECKLIST}_SUCCESS`: {
        const array = payload.body;
        for (let i = 0; i < array.length; i += 1) {
          const obj = array[i];
          modelClass.create(obj);
        }
        break;
      }
      default:
        break;
    }
    return null;
  }
}

Checklist.modelName = 'Checklist';

Checklist.fields = {
  id: attr(),
  text: attr(),
  status: attr(),
  created_at: attr(),
  task_id: fk('Task', 'checklist'),
};

export default Checklist;
