import { attr, Model } from 'redux-orm';

import {
  FETCH_CREATE_MILESTONE,
  FETCH_UPDATE_MILESTONE,
  FETCH_DELETE_MILESTONE,
  FETCH_GET_ALL_MILESTONES,
  FETCH_GET_ALL_PROJECT_MILESTONES,
  FETCH_MILESTONE_ASSIGN,
  FETCH_MILESTONE_UNASSIGN,

} from '../../constants/api/milestones';

class Milestone extends Model {
  static create(props) {
    if (props.hasOwnProperty(this.idAttribute) && this.idExists(props[this.idAttribute])) {
      const model = this.withId(props.id);
      return model.update(props);
    }
    return super.create(props);
  }

  static reducer(action, modelClass) {
    const { type, payload } = action;
    switch (type) {
      case `${FETCH_CREATE_MILESTONE}_SUCCESS`:
        modelClass.create({ ...payload.body});
        break;
      case `${FETCH_UPDATE_MILESTONE}_SUCCESS`:
        modelClass.withId(payload.body.id).update(payload.body);
        break;
      case `${FETCH_DELETE_MILESTONE}_SUCCESS`:
        modelClass.withId(payload.body.id).delete();
        break;
      case `${FETCH_GET_ALL_MILESTONES}_SUCCESS`: {
        const array = payload.body.results || payload.body;
        for (let i = 0; i < array.length; i += 1) {
          const obj = array[i];
          modelClass.create({ ...obj, project: obj.project_id });
        }
        break;
      }
      case `${FETCH_GET_ALL_PROJECT_MILESTONES}_SUCCESS`: {
        const array = payload.body.results || payload.body;
        for (let i = 0; i < array.length; i += 1) {
          const obj = array[i];
          modelClass.create(obj);
        }
        break;
      }
      case `${FETCH_MILESTONE_ASSIGN}_SUCCESS`: {
        const task = modelClass.withId(payload.body.task_id);
        const newTask = Object.assign({}, task, {
          assigned: task.assigned.slice(0),
        });
        newTask.assigned.push(payload.body.milestone_id);
        modelClass.withId(payload.body.task_id).update(newTask);
        break;
      }
      case `${FETCH_MILESTONE_UNASSIGN}_SUCCESS`: {
        const task = modelClass.withId(payload.body.task_id);
        const newTask = Object.assign({}, task, {
          assigned: task.assigned.filter(milestoneId => milestoneId !== payload.body.milestone_id),
        });
        modelClass.withId(payload.body.task_id).update(newTask);
        break;
      }
      default:
        break;
    }
    return null;
  }
}

Milestone.modelName = 'Milestone';

Milestone.fields = {
  milestone_id: attr(),
  milestone_name: attr(),
  project_id: attr(),
};

export default Milestone;
