import { attr, Model } from 'redux-orm';

import {
  FETCH_CREATE_LABEL,
  FETCH_UPDATE_LABEL,
  FETCH_DELETE_LABEL,
  FETCH_GET_ALL_LABELS,
} from '../../constants/api/labels';

class Label extends Model {
  static create(props) {
    if (props.hasOwnProperty(this.idAttribute) && this.idExists(props[this.idAttribute])) {
      const model = this.withId(props.id);
      return model.update(props);
    }
    return super.create(props);
  }

  static reducer(action, modelClass) {
    const { type, payload } = action;
    switch (type) {
      case 'PUSHER_UPDATE_LABEL': {
        modelClass.withId(payload.id).update({ ...payload });
        break;
      }
      case `${FETCH_CREATE_LABEL}_SUCCESS`: {
        const body = { ...payload.body, id: payload.body.label_id };
        modelClass.create(body);
        break;
      }
      case `${FETCH_UPDATE_LABEL}_SUCCESS`: {
        modelClass.withId(payload.body.label_id).update(payload.body);
        break;
      }
      case `${FETCH_DELETE_LABEL}_SUCCESS`: {
        modelClass.withId(payload.body.label_id).delete();
        break;
      }
      case `${FETCH_GET_ALL_LABELS}_SUCCESS`: {
        const array = payload.body.labels;
        for (let i = 0; i < array.length; i += 1) {
          const obj = array[i];
          modelClass.create(obj);
        }
        break;
      }
      default:
        break;
    }
    return null;
  }
}

Label.modelName = 'Label';

Label.fields = {
  id: attr(),
  name: attr(),
  color: attr(),
};

export default Label;
