import React, { Fragment } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Login from '../components/login/LoginContainer';

const PrivateComponent = ({ id, isAuthenticated, children }) => (
  <Fragment>
    {
      isAuthenticated ? (
        <Fragment>
          <df-messenger
            chat-icon="https://zmartbot-files.s3-sa-east-1.amazonaws.com/isotipo_normal.png"
            intent="WELCOME"
            chat-title="ZmartBot"
            agent-id="6e5180cd-9620-4ed0-8359-2459a8d40e88"
            language-code="es"
            user-id={id}
          />
          {children}
        </Fragment>
      ) : (
        <Login />
      )
    }
  </Fragment>
);

PrivateComponent.propTypes = {
  id: PropTypes.string.isRequired,
  isAuthenticated: PropTypes.bool.isRequired,
  children: PropTypes.node.isRequired,
};

const mapStateToProps = (state) => {
  return {
    id: state.auth.id,
    isAuthenticated: state.auth.isAuthenticated,
    token: state.auth.token,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(PrivateComponent);
