import { fk, attr, Model } from 'redux-orm';

import {
  FETCH_ADD_ATTACHMENT,
  FETCH_GET_ATTACHMENT,
  FETCH_DELETE_ATTACHMENT,
} from '../../constants/api/attachments';

class Attachment extends Model {
  static create(props) {
    if (props.hasOwnProperty(this.idAttribute) && this.idExists(props[this.idAttribute])) {
      const model = this.withId(props.id);
      return model.update(props);
    }
    return super.create(props);
  }

  static reducer(action, modelClass) {
    const { type, payload } = action;
    switch (type) {
      case `${FETCH_ADD_ATTACHMENT}_SUCCESS`: {
        const body = { ...payload.body, id: payload.body.id };
        modelClass.create(body);
        break;
      }
      case `${FETCH_DELETE_ATTACHMENT}_SUCCESS`:
        modelClass.withId(payload.body.id).delete();
        break;
      case `${FETCH_GET_ATTACHMENT}_SUCCESS`: {
        const array = payload.body;
        for (let i = 0; i < array.length; i += 1) {
          const obj = array[i];
          modelClass.create(obj);
        }
        break;
      }
      default:
        break;
    }
    return null;
  }
}

Attachment.modelName = 'Attachment';

Attachment.fields = {
  id: attr(),
  name: attr(),
  key: attr(),
  created_at: attr(),
  task_id: fk('Task', 'attachments'),
};

export default Attachment;
