import { attr, Model } from 'redux-orm';

import {
  FETCH_CREATE_MEMBER,
  FETCH_UPDATE_MEMBER,
  FETCH_DELETE_MEMBER,
  FETCH_GET_ALL_MEMBERS,
} from '../../constants/api/members';

class Member extends Model {
  static create(props) {
    if (props.hasOwnProperty(this.idAttribute) && this.idExists(props[this.idAttribute])) {
      const model = this.withId(props.id);
      return model.update(props);
    }
    return super.create(props);
  }

  static reducer(action, modelClass) {
    const { type, payload } = action;
    switch (type) {
      case `${FETCH_CREATE_MEMBER}_SUCCESS`:
        modelClass.create(payload.body);
        break;
      case `${FETCH_UPDATE_MEMBER}_SUCCESS`:
        modelClass.withId(payload.body.member_id).update(payload.body);
        break;
      case `${FETCH_DELETE_MEMBER}_SUCCESS`:
        modelClass.withId(payload.body.member_id).delete();
        break;
      case `${FETCH_GET_ALL_MEMBERS}_SUCCESS`: {
        const array = payload.body.members;
        for (let i = 0; i < array.length; i += 1) {
          const obj = array[i];
          modelClass.create(obj);
        }
        break;
      }
      default:
        break;
    }
    return null;
  }
}

Member.modelName = 'Member';

Member.fields = {
  id: attr(),
  name: attr(),
  role: attr(),
  email: attr(),
};

export default Member;
